import { CheckCircle2, Heart, Leaf, ShieldCheck, Target } from "lucide-react"
import Link from "next/link"
import { Button } from "@/components/ui/button"

const checklist = [
  "Certified & Experienced Dieticians",
  "Science-Based Nutrition Plans",
  "Personalized Approach to Health",
  "Ongoing Support & Monitoring",
]

const featureCards = [
  {
    title: "Trusted Care",
    description: "Evidence-based approaches",
    icon: ShieldCheck,
  },
  {
    title: "Goal Focused",
    description: "Achieve your health targets",
    icon: Target,
  },
  {
    title: "Holistic Health",
    description: "Mind & body wellness",
    icon: Heart,
  },
  {
    title: "Natural Solutions",
    description: "Sustainable nutrition",
    icon: Leaf,
  },
]

export default function AboutPage() {
  return (
    <main className="bg-white">
      <section className="px-4 py-24 bg-linear-to-br from-[#EAF7F1] via-white to-[#EEF5FF]">
        <div className="mx-auto max-w-4xl text-center">
          <p className="text-sm font-semibold uppercase tracking-[0.3em] text-primary/80">About Us</p>
          <h1 className="mt-4 text-4xl font-bold text-foreground md:text-5xl">Transforming Lives Through Nutrition</h1>
          <p className="mt-6 text-lg text-foreground/70">
            Dietify pairs compassionate coaching with evidence-based nutrition to help you build habits that last.
            Whether you are just starting your wellness journey or managing a chronic condition, we walk beside you at
            every step.
          </p>
        </div>
      </section>

      <section className="px-4 py-20">
        <div className="mx-auto flex max-w-6xl flex-col gap-16 md:flex-row md:items-center">
          <div className="flex-1">
            <h2 className="text-3xl font-bold text-foreground md:text-4xl">Our Philosophy</h2>
            <p className="mt-6 text-lg leading-relaxed text-foreground/70">
              Optimal health starts with food that fuels both body and mind. Our certified dieticians craft meal plans
              that honor your culture, schedule, and medical history, making nutrition feel approachable and sustainable.
            </p>
            <p className="mt-4 text-lg leading-relaxed text-foreground/70">
              We combine regular check-ins, progress tracking, and practical resources—recipes, grocery guidance, and
              habit-building tips—so you can stay confident throughout your transformation.
            </p>

            <div className="mt-8 space-y-4">
              {checklist.map((item) => (
                <div key={item} className="flex items-start gap-3 text-foreground">
                  <CheckCircle2 className="mt-1 h-5 w-5 text-primary" />
                  <span className="text-base font-medium text-foreground/80">{item}</span>
                </div>
              ))}
            </div>
          </div>

          <div className="flex-1">
            <div className="grid gap-6 md:grid-cols-2">
              {featureCards.map((feature) => (
                <div
                  key={feature.title}
                  className="group relative overflow-hidden rounded-3xl border border-white/40 bg-linear-to-br from-[#F0FBF7] via-white to-[#EEF5FF] p-6 shadow-[0_25px_60px_rgba(20,128,90,0.12)] transition-all duration-500 transform-gpu hover:-translate-y-3 hover:scale-105 hover:shadow-[0_35px_90px_rgba(20,128,90,0.18)]"
                >
                  <div className="pointer-events-none absolute inset-0 opacity-0 transition-opacity duration-500 group-hover:opacity-100">
                    <div className="absolute -top-16 right-0 h-32 w-32 rounded-full bg-primary/20 blur-3xl" />
                  </div>
                  <div className="relative z-10">
                    <div className="mb-4 flex h-12 w-12 items-center justify-center rounded-xl bg-primary/10 text-primary transition-transform duration-500 group-hover:-translate-y-1 group-hover:scale-105">
                      <feature.icon className="h-6 w-6" />
                    </div>
                    <h3 className="text-lg font-semibold text-foreground">{feature.title}</h3>
                    <p className="mt-1 text-sm text-foreground/60">{feature.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </section>

      <section className="bg-primary px-4 py-16 text-primary-foreground">
        <div className="mx-auto max-w-4xl text-center">
          <h2 className="text-3xl font-bold">Ready to Work With Our Team?</h2>
          <p className="mt-4 text-lg opacity-90">
            Schedule a consultation to receive a customized plan and continuous support from our certified dieticians.
          </p>
          <Link href="/book" className="mt-8 inline-flex">
            <Button size="lg" variant="secondary">
              Book Your Appointment
            </Button>
          </Link>
        </div>
      </section>
    </main>
  )
}
